
body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    color: #212121 !important;
    overflow-x: hidden;
    max-height: auto;
  }
  
  h3 {
    font-weight: 300;
  }
  
  h6 {
    font-weight: 500;
  }
  
  p, span {
    font-weight: 300;
  }
  
  p {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .section {
    padding-left: 5%;
    padding-right: 5%;
    position: relative;
  }
  
  .section-container {
    width: 100%;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 160px;
    padding-bottom: 160px;
  }
  
  .section.dark {
    background: #16151C;
  }
  
  .section-container.header-component {
    padding: 0;
  }
  
  .section-container.footer-component {
    padding: 0;
  }
  
  .section.colored {
    background: #D8AB94;
    /* background: #EBEBEB; */
  }
  
  p.headline {
    margin-top: 0;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 3px;
    font-size: 10pt;
    opacity: 0.8;
  }
  
  .header-section {
    background: #16151C;
  }
  
  p.text-justify {
    text-align: justify;
  }
  
  p.text-center {
    text-align: center;
  }

  .header {
    text-align: center;
    position: absolute;
    top: 48%;
    transform: translateY(-50%);
    width: 100%;
   color: white;
  }
  
  
  .header-wrapper {
    position: relative;
  }
  
  .scroll-down-btn {
    text-align: center;
    position: absolute;
    bottom: 35px;
    text-align: center;
    width: 100%;
  }
  
  .header h6 {
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 6px;
    font-size: 12px;
  }
  
  .header h3 {
    text-transform: uppercase;
    letter-spacing: 9px;
    font-weight: 300;
    font-size: 30px;
  }
