  
  .header-wrapper {
  }
  
  .header-skill {
    text-align: center;
    position: absolute;
    top: 13%;
    transform: translateY(-50%);
    width: 100%;
   color: white;
  }
  
  .header-skill h6 {
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 6px;
    font-size: 12px;
  }
  
  .header-skill h3 {
    text-transform: uppercase;
    letter-spacing: 9px;
    font-weight: 300;
    font-size: 30px;
  }

  .skill-container {
    position: absolute;
    top: 25%;
    width: 100%;
   color: white;
   overflow-y: scroll;
   height: 70% !important;

   
  }

  .skill-box {
 
    padding-top: 8px;
    padding-bottom: 8px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 15px;
  }



  .skill-progress {
    height: 10px !important;
    background-color:  rgb(255, 140, 179) !important;
    
  }

  .skill-name {
    padding-bottom: 8px;
  }

  ::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}